@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Container {
    position: relative;
    height: 9vh;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    margin-bottom: 0px;
}

.Video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: relative;
  top: 0;
  left: 0;
}
@media (max-width: 768px) {
  .Container{
    width: 100vw;
  }
  }
  
  /* Add some content at the bottom of the video/page */
  .Content {
    position: absolute;
    top: 0;
    background-color: rgba(43, 26, 26, 0.4);
    color:#f1f1f1;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  .SubContent {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 10px;
    }
  .SubContent p {
      width: 90vw;
      color: #eeeeeead;
  }
  .SubContent button {
      font-weight: bold;
      margin-top: 30px;
      color: #f1f1f1;
      border: #f1f1f1 1px solid;
      padding: 20px 25px;
      background-color: hsla(0, 0%, 0%, 0);
      font-size: 1.25rem;
      width: 150px;
      border-radius: 3px;
  }
  .SubContent button:hover {
      background-color: #f1f1f1;
      color: #000000c0;
  }

  .SubContent img {
    width: 200px;
    height: 200px;
    display: block;
    margin: 20px 0px 20px 10px;
  }
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}.wrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(12rem, 16rem));grid-gap:2rem;gap:2rem;justify-content:center}.card{overflow:hidden;box-shadow:0 2px 20px #e1e5ee;border-radius:.2rem;display:flex;flex-direction:column;justify-content:space-between;cursor:pointer;transition:transform 200ms ease-in}.card__image{height:25rem;width:100%;object-fit:cover}.card__title{padding:1rem;font-size:3rem}.card__description{padding:0 3rem;justify-content:left}.card__btn{padding:1rem;font-family:inherit;font-weight:bold;font-size:2rem;margin:1rem;border:2px solid #d50000;background:transparent;color:#d50000;border-radius:.2rem;transition:background 200ms ease-in, color 200ms ease-in}.card:hover{transform:scale(1.02)}.card:hover .card__btn{background:#d50000;color:white}

